import React, { Component } from 'react'

export default class Entry extends Component {

    state = {
        title :this.props.title,
        content: this.props.content
    }

    render() {
        return (
            <div className="post" style={styles.post}>
                {
                    this.props.images ? this.props.images.map(image => 
                        <img key={image.url} style={styles.image} src={image.url}></img>
                    ) :
                        null
                }
                <div style={styles.content}>
                    <ul className="tags" style={styles.tags}>
                        {
                            this.props.tags.map(tag => 
                                <li style={styles.tag} key={tag + this.props.id} >{tag}</li>
                            )
                        }
                    </ul>
                    <h2 style={{textAlign: 'center', marginBottom: '-5px', fontSize: '2em' }}>{this.state.title}</h2>
                    <p style={{textAlign: 'center', color: '#ececec'}}>{this.props.date}</p>
                    <p style={{ textAlign: 'center' }}>{this.state.content}</p>
                </div>
            </div>
        )
    }
}

const styles = {
    tags: {
        display: 'flex',
        listStyle: 'none',
        marginLeft: '-40px',
        fontSize: '12px',
        justifyContent: 'center',
        marginBottom: '-5px'
    },

    tag: {
        padding: '5px 20px',
        margin: '0 10px',
        borderRadius: '2px',
        backgroundColor: '#F8054D',
        color: 'white'
    },

    post: {
        //backgroundColor: '#F8054D',
        backgroundColor: '#ececec',
        //border: '1px solid #cecece80',
        overflow: 'hidden',
        borderRadius: '5px',
        width: '100%',
        marginBottom: '2em',
    },
     
    image : {
        width: "100%",
        marginBottom: '-1em'
    },

    content: {
        padding: '3em'
    }

}